<template>
  <div class="login">
    <div class="logo">
      <div>
        <img src="../../assets/images/网易云音乐.svg" alt />
      </div>
    </div>
    <div class="logo-bottom">
      <div class="phone" >{{phone==""? '请选择账号':phone }}  <span @click="gologin" class="iconfont">&#xe639;</span></div>
      <div class="chinamove">中国移动提供认证服务</div>
      <div class="one-login">
        <van-button type="primary" size="large">一 键 登 录</van-button>
      </div>
      <div class="not-login">
        <van-button type="primary" size="large" @click="login">立 即 注 册</van-button>
      </div>
      <div class="login-way">
        <div class="iconfont" v-for="(value,index) in loginway" :key="index">
          <div v-html="value.icon"></div>
        </div>
      </div>
    
      <div class="agreement">
        <van-checkbox v-model="checked">
          同意
          <a href>用户协议</a>
          <a href>用户协议</a>
          <a href>用户协议</a>
      
        </van-checkbox>
      </div>
    </div>
  </div>
</template>

<script>
import "../../assets/css/Login.less";



import {mapState} from "vuex"
export default {
  data() {
    return {
      // phone:"",
      checked: true,
      loginway: [
        { icon: " &#xe502;" },
        { icon: "&#xe64b;" },
        { icon: "&#xe504;" },
        { icon: "&#xe659;" },
      ],
    };
  },
  computed: {
     
          phone(){
        
               
           console.log("aaa",this.$store.state.infostore.phone);

            if (!this.$store.state.infostore.phone) {
              return ""
            }
              return  this.$store.state.infostore.phone.substr(0,3)+"****"+this.$store.state.infostore.phone.substr(7);
          }

    ,
  },
  
  created(){
     
      // console.log(this.$route.query.userInfo);
  },
  methods: {
    // 注册
    login() {
     this.$router.push({name:"Register"})
        // this.$store.commit("delects")
    },
    // 登录
    gologin(){
      
        this.$router.push({name:"Selphone"})
    }
  },
};
</script>

