<template>
  <div class="main">
    <van-nav-bar fixed left-arrow @click-left="onClickLeft">
      <template #left>
        <div class="iconfont">&#xe629;</div>
      </template>
      <template #title></template>
      <template #right>
        <span class="iconfont">&#xe65f;</span>
      </template>
    </van-nav-bar>

    <div class="title">

      <van-tabbar v-model="active"  @change="changes" :route="true" :fixed="false" class="van-tabbar">
        <van-tabbar-item
          v-for="(value,index) in dataobj"
          :key="index"
          @click="GO(value)"
          class="typeDesc"
          :class="active==index? 'active':''"
        >{{value.title}}</van-tabbar-item>

      </van-tabbar>

    </div>
    <div class="item">
  <router-view></router-view>
    </div>
 
 

     

  </div>
</template>

<script>
import "../assets/css/Main.less";

export default {
  data() {
    return {
      active: 0,
      dataobj: [
        {
          title: "我的",
          name: "My",
          urls: ["My"],
        },
        {
          title: "发现",
          name: "Fine",
          urls: ["Fine"],
        },
        {
          title: "云村",
          name: "City",
          urls: ["City"],
        },
        {
          title: "视频",
          name: "Movie",
          urls: ["Movie"],
        },
      ],
    };
  },
  methods: {
    onClickLeft() {},
    GO(value) {
      this.$router.push({ name: value.name });
    },
    changes() {
      console.log(this.active);
    },
  },
};
</script>

<style lang="less" scoped>
@font-face {
  font-family: "../assets/font/iconfont";
  src: url("../assets/font/iconfont.eot");
  src: url("../assets/font/iconfont.eot?#iefix") format("embedded-opentype"),
    url("../assets/font/iconfont.woff2") format("woff2"),
    url("../assets/font/iconfont.woff") format("woff"),
    url("../assets/font/iconfont.ttf") format("truetype"),
    url("../assets/font/iconfont.svg#iconfont") format("svg");
}
