<template>
<div class="player">
    <div class="mask" :style="setbgsty"></div>
    <div class="masks"></div>
    <van-nav-bar left-arrow @click-left="back">
        <template #left>
            <div class="iconfont">&#xe501;</div>
            <div class="music-name">
                <div class="name">{{audiodetail.name}}</div>
                <div class="info">
                    {{audiodetail.ar[0].name }}
                    <span class="iconfont">&#xe527;</span>
                </div>
            </div>
        </template>
        <template #right>
            <div class="iconfont">&#xe738;</div>
        </template>
    </van-nav-bar>
    <div class="player-body" :class="{active:status==1? true:false}" @click="changeshow">
        <div class="circle" v-show="isshow">
            <div class="pointer">
                <img src="../assets/images/cd_tou.png" alt />
            </div>

            <div class="bgwhite"></div>

            <div class="player-roud">
                <van-swipe class="my-swipe" initial-swipe="1" indicator-color="white" ref="swipe" :show-indicator="false" @change="changeswipe">
                    <van-swipe-item v-for="(value,index) in blackbg" :key="index">
                        <div class="bgbalck">
                            <div class="urlbg">
                                <img :src="value.bg" alt />
                            </div>
                        </div>
                    </van-swipe-item>
                </van-swipe>
            </div>
        </div>
        <div class="lyricfather" v-show="!isshow">
            <div class="volume">
                <div class="iconfont">&#xe662;</div>
                <div class="range">
                    <van-slider v-model="volumevalue" inactive-color="#626262" active-color="#979797" @change="onChangevolume" max="100" />
                </div>
            </div>

            <div class="lyric" ref="scrolltop" @touchend="touchends" @touchmove="touchmoves">
                <div class="lyric-list" v-for="(value,key,index) in lyricArray" :class="{active:key==keys? true:false}" :key="index" :id="index">{{value}}</div>
            </div>
        </div>
    </div>

    <div class="player-bottom">
        <div class="progress">
            <div class="star">{{nowtime}}</div>
            <div class="range">
                <van-slider v-model="value" :max="max" :class="{active:ischeak}" @input="ipt" @change="changetime" inactive-color="#626262" active-color="#979797" />
            </div>
            <div class="end">{{totaltime}}</div>
        </div>

        <div class="control">
            <div class="pattern">
                <div v-for="(value,index) in pattern" :key="index" v-show="value.ischeak" class="iconfont" v-html="value.icon" @click="changepattern(value,index)"></div>
            </div>
            <div class="iconfont prev" @click="perv">&#xe508;</div>
            <div class="iconfont" @click.stop="play">{{status == 0 ? '&#xe608;' : '&#xe664;'}}</div>
            <div class="iconfont next" @click="playernext">&#xe50b;</div>
            <div class="iconfont list" @click.stop="popup">&#xe663;</div>
        </div>
    </div>
    <van-popup v-model="show" round position="bottom">
        <div class="nowplay">
            当前播放
            <span class="nownum">({{audiocoollets.length}})</span>
        </div>
        <div class="control">
            <div class="pattern">
                <div v-for="(value,index) in pattern" :key="index" v-show="value.ischeak" class="iconfont" @click="changepattern(value,index)">
                    <span v-html="value.icon"></span>
                    <span>{{value.title}}</span>
                </div>
            </div>
        </div>
        <div class="audiocoollets-list">
            <div class="listcoollets-item" v-for="(value,index) in audiocoollets" :key="index" :class="[index==audioindex? 'active':'']" @click="playmusic(value,index)">
                <div class="iconfont" v-if="index==audioindex">&#xe662;</div>
                <div class="listname">{{value.name}}</div>-
                <div class="itemname">{{value.ar[0].name}}</div>
            </div>
        </div>
    </van-popup>
</div>
</template>

<script>
import "../assets/css/playermusic.less";
import {
    createNamespacedHelpers,
    mapState,
    mapMutations,
    mapActions,
} from "vuex";
const {
    mapState: playermusicState,
    mapActions: playermusicActions,
} = createNamespacedHelpers("playermusicstore");
export default {
    data() {
        return {
            // 音量大小
            show: false,
            volumevalue: 0,
            isshow: true,
            false: false,
            autoplay: 0,
            pattern: [{
                    icon: "&#xe506;",
                    start: 0,
                    ischeak: false,
                    title: "单曲循环"
                },
                {
                    icon: "&#xe619;",
                    start: 1,
                    ischeak: false,
                    title: "随机"
                },
                {
                    icon: "&#xe60b;",
                    start: 2,
                    ischeak: false,
                    title: "列表循环"
                },
            ],
            // 当前进度条位置
            value: 0,
            ischeak: false,
            // 改变进度条时让歌继续放
            reset: false,
            // g滚动歌词时让歌词别再滚动
            scrollreset: true,
            // 进度条最大值
            max: 0,
            //  复制歌词
            lyricArraycopy: "",
            // 歌词显示的下标
            keys: 0,
            // 亮的那个歌词下标
            index: 0,
            // 歌词的key
            lyricArraykeys: "",
            // 获取歌词dom
            // scrolltops: "",
            // 定时器
            setTimer: null,
            // 轮盘dom
            swipe: null,
        };
    },
    computed: {
        ...playermusicState([
            "audioSrc", //音频url
            "player", //播放器dom
            "audio", //音频dom
            "status", //音频状态
            "audiodetail", //音频详情
            "lyricArray", //音频歌词
            "audioindex", //音频下标
            "patternstatus", //播放顺序
            "audiocoollets", //正在播放的音乐所属的地方的列表
            "playedMusicList", //已播放过的音乐列表
            "timeupdates", //当前播放的时间
            "scrolltops", //当前歌词滚动的距离
            "playervolume", //当前的音量
        ]),
        ...mapState(["shows"]),
        //总时长
        totaltime() {
            // console.log('data ==> ', data);
            //获取音频时间
            let duration = this.max;
            let minutes = Math.floor(duration / 60);
            minutes = minutes >= 10 ? minutes : "0" + minutes;
            let seconds = Math.floor(duration % 60);
            seconds = seconds >= 10 ? seconds : "0" + seconds;
            duration = minutes + ":" + seconds;
            return duration;
        },
        // 当前的时长
        nowtime() {
            let duration = this.value;
            let minutes = Math.floor(duration / 60);
            minutes = minutes >= 10 ? minutes : "0" + minutes;
            let seconds = Math.floor(duration % 60);
            seconds = seconds >= 10 ? seconds : "0" + seconds;
            duration = minutes + ":" + seconds;
            return duration;
        },
        // 设置背景样式
        setbgsty() {
            let playermusicstore = this.$store.state.playermusicstore;
            let picUrl;
            if (playermusicstore.audiodetail == "") {
                picUrl = "aaa";
            } else {
                picUrl = playermusicstore.audiodetail.al.picUrl;
            }
            return {
                backgroundImage: `url(${picUrl}) `,
            };
        },
        // 黑胶图片
        blackbg() {
            // 获取当前播放的下标
            let audioindex = this.$store.state.playermusicstore.audioindex;
            console.log("audioindex", audioindex);
            // 获取当前列表
            let audiocoollets = this.$store.state.playermusicstore.audiocoollets;

            return [{
                    bg: audiocoollets[audioindex].al.picUrl
                },
                {
                    bg: audiocoollets[audioindex].al.picUrl
                },
                {
                    bg: audiocoollets[audioindex].al.picUrl
                },
            ];
        },
        // 设置滚动
        scrolltop() {
            return {
                transform: `translate3d(0px, -${this.index * 26}px, 0px)`,
                transitionDuration: "0ms",
                transitionproperty: "none",
            };
        },
    },
    created() {
        this.timeupdate();

        this.durationchange();
        // 初始化音量

        this.volumevalue = this.playervolume * 100;
        // 复制歌词
        this.lyricArraycopy = this.$store.state.playermusicstore.lyricArray;
        // 将歌词的key以一个数组的方式保存
        this.lyricArraykeys = Object.keys(this.lyricArraycopy);

        this.$nextTick(() => {
            console.log(this.$refs.scrolltop);
            let scroll = this.$refs.scrolltop;
            this.setscrolltops(scroll);
        });
        this.$nextTick(() => {
            console.log(this.$refs.swipe);
            this.swipe = this.$refs.swipe;
        });

        this.pattern.map((v) => {
            if (v.start == this.$store.state.playermusicstore.patternstatus) {
                v.ischeak = true;
            } else {
                v.ischeak = false;
            }
        });
        this.max = this.audio.duration;

        this.player.style.display = "none";
    },
    mounted() {},
    beforeRouteLeave(to, from, next) {
        next(true);
        console.log(this);
        this.player.style.display = "block";
        this.player.style.display = "fixed";
    },
    methods: {
        ...playermusicActions([
            "changeMusicStatus",
            "changestatus",
            "changeAudioindex",
            "changeAudioSrc",
            "addPlayedMusic",
            "parseLyric",
            "setscrolltops",
            "changeplayervolume",
        ]),

        back() {
            this.$router.push({
                name: "My"
            })
        },
        //  显示歌词或者转盘
        changeshow() {
            this.isshow = !this.isshow;
        },
        touchmoves() {
            this.setTimer = null;
            this.scrollreset = false;
        },
        touchends() {
            this.setTimer = setTimeout(() => {
                this.scrollreset = true;
            }, 1000);
        },

        // 时长发生变化
        timeupdate() {
            // class="{active:index==timeupdates? true:false}"
            this.audio.ontimeupdate = () => {
                let time = Math.floor(this.audio.currentTime);

                this.lyricArraykeys.map((v, index) => {
                    if (v == time) {
                        this.keys = v;
                        this.index = index;
                        return;
                    }
                });

                if (this.reset) {
                    return;
                }

                if (this.scrolltops && this.scrollreset) {
                    this.scrolltops.scrollTop = this.index * 26;
                }

                this.value = this.audio.currentTime;
            };
        },
        //换音乐时
        durationchange() {
            this.audio.ondurationchange = () => {
                this.max = this.audio.duration;
            };
        },
        // 播放结束
        // ended() {
        //   this.audio.onended = () => {
        //     let audioindex = this.audioindex;
        //     console.log("this.patternstatus", this.patternstatus);
        //     if (this.patternstatus == 1) {
        //       audioindex = Math.floor(Math.random() * this.audiocoollets.length);
        //       this.changeAudioindex(audioindex).then((result) => {
        //         console.log("xudio", result);
        //         this.read(result);
        //       });
        //     } else if (this.patternstatus == 2) {
        //       audioindex++;
        //       if (audioindex > this.audiocoollets.length - 1) {
        //         audioindex = 0;
        //       }

        //       this.changeAudioindex(audioindex).then((result) => {
        //         console.log("xudio", result);
        //         this.read(result);
        //       });
        //     } else {
        //       audioindex = this.audioindex;
        //       this.changeAudioindex(audioindex).then((result) => {
        //         console.log("xudio", result);
        //         this.read(result);
        //       });
        //     }
        //   };
        // },
        // 改变播放顺序状态
        changepattern(value, index) {
            value.ischeak = false;

            let status = index;
            status++;
            if (status >= 3) {
                status = 0;
            }
            this.pattern[status].ischeak = true;
            this.changestatus(status);
        },
        // 拖动进度条
        ipt() {
            this.ischeak = true;
            this.reset = true;
        },
        // 改变音量
        onChangevolume() {
            let volume = this.volumevalue / 100;
            this.audio.volume = volume;
            this.changeplayervolume(volume);
        },
        // 结束拖动
        changetime() {
            this.ischeak = false;
            this.audio.currentTime = this.value;
            this.reset = false;
        },
        // 开始暂停
        play() {
            let playStatus = this.status == 0 ? "play" : "pause";
            let status = this.status == 0 ? 1 : 0;
            this.changeMusicStatus(status).then(() => {
                this.audio[playStatus]();
            });
        },
        // 左右滑动转盘
        changeswipe(index) {
            console.log("index======>", index);
        },
        // 下一首
        playernext() {
            this.swipe.next();

            let audioindex = this.audioindex;
            if (this.patternstatus == 1 || this.patternstatus == 0) {
                audioindex = Math.floor(Math.random() * this.audiocoollets.length);
                this.changeAudioindex(audioindex).then(() => {
                    this.read(this.audioindex);
                });
            } else {
                audioindex++;
                if (audioindex > this.audiocoollets.length - 1) {
                    audioindex = 0;
                }
                this.changeAudioindex(audioindex).then(() => {
                    this.read(this.audioindex);
                });
            }
        },

        // 上一首
        perv() {
            this.swipe.prev();
            let audioindex = this.audioindex;
            audioindex--;
            if (audioindex < 0) {
                audioindex = this.audiocoollets.length - 1;
            }
            this.changeAudioindex(audioindex).then(() => {
                this.read(this.audioindex);
            });
        },
        // 设置播放url
        read(index) {
            console.log("index", index);

            let item = this.audiocoollets[index];
            let id = this.audiocoollets[index].id;

            if (item.url == null) {
                console.log("下一首不存在");
                this.playernext();
                console.log("下一首不存在");
                return;
            }

            this.axios({
                    method: "GET",
                    url: "/lyric",
                    withCredentials: true,
                    params: {
                        id: id,
                        //   id: 4341314,
                    },
                })
                .then((result) => {
                    console.log("请求歌词成功", result);
                    let lyric;
                    if (!result.data.needDesc) {
                        lyric = result.data.lrc.lyric;
                    } else {
                        lyric = "纯音乐。请您欣赏";
                    }

                    this.parseLyric(lyric).then((result) => {
                        this.lyricArraycopy = this.lyricArray;
                        this.lyricArraykeys = Object.keys(this.lyricArraycopy);
                        this.keys = Number(Object.keys(this.lyricArraycopy)[0]);

                        console.log(" this.keys", this.keys);
                    });
                })
                .catch((err) => {
                    console.log(err);
                });

            this.changeAudioSrc(item).then((audio) => {
                audio.currentTime = 0;
                //添加播放的音乐
                this.addPlayedMusic(item);
            });
        },

        // 上拉列表
        popup() {
            this.show = !this.show;
        },
        playmusic(item, index) {
            console.log(item);
            console.log(!null);
            if (item.url == null) {
                this.$toast("因api不提供url原因，该资源无法播放");
                return;
            }

            this.axios({
                    method: "GET",
                    url: "/lyric",
                    withCredentials: true,
                    params: {
                        id: item.id,
                        //   id: 4341314,
                    },
                })
                .then((result) => {
                    console.log("请求歌词成功", result);
                    let lyric;
                    if (!result.data.needDesc) {
                        lyric = result.data.lrc.lyric;
                    } else {
                        lyric = "纯音乐。请您欣赏";
                    }
                    this.parseLyric(lyric);
                })
                .catch((err) => {
                    console.log(err);
                });
            this.changeAudioindex(index);
            this.changeAudioSrc(item).then((audio) => {
                audio.currentTime = 0;
                //添加播放的音乐
                this.addPlayedMusic(item);
            });
        },
    },
};
</script>

<style lang="less" scoped>
@font-face {
    font-family: "../assets/font/iconfont";
    src: url("../assets/font/iconfont.eot");
    src: url("../assets/font/iconfont.eot?#iefix") format("embedded-opentype"),
        url("../assets/font/iconfont.woff2") format("woff2"),
        url("../assets/font/iconfont.woff") format("woff"),
        url("../assets/font/iconfont.ttf") format("truetype"),
        url("../assets/font/iconfont.svg#iconfont") format("svg");
}
</style>
