<template>
  <div class="recently">
    <van-nav-bar left-arrow fixed>
      <template #right>
        <div>清空</div>
      </template>
      <template #left>
        <div class="iconfont back" @click="back">&#xe501;</div>
        <div>最近播放</div>
      </template>
    </van-nav-bar>

    <div class="likelist-body">
      <div class="likelist-body-title">
        <div class="ico">
          <div class="iconfont">&#xe653;</div>
        </div>
        <div class="playall">
          播放全部
          <span>（共{{playedMusicList.length}}首）</span>
        </div>
      </div>
      <div class="likelist-body-body">
        <div
          class="list-item"
          v-for="(value,index) in playedMusicList"
          :key="value.id"
          @click="playAudio(value,index)"
        >
          <div class="index">{{index+1}}</div>
          <div class="likelistmusic-name" :class="{ active: !value.url}">
            <div class="music-name">
              <div>{{value.name}}</div>
              <div v-show="value.alia[0]">({{value.alia[0]}})</div>
            </div>
            <div class="musicinfo-name">
              <div>{{value.ar[0].name}}-{{value.al.name}}</div>
            </div>
          </div>
          <div class="set">
            <div class="iconfont">&#xe653;</div>
            <div class="iconfont">&#xe609;</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/css/recentlyplay.less";
import { createNamespacedHelpers, mapState, mapMutations } from "vuex";
const {
  mapState: playermusicState,
  mapActions: playermusicActions,
} = createNamespacedHelpers("playermusicstore");
export default {
  data() {
    return {
      active: 1,
      tablist: [
        { title: "歌曲" },
        { title: "歌曲" },
        { title: "歌曲" },
        { title: "歌曲" },
      ],
      //   需要显示的列表
      listitem: "",
    };
  },
  computed: {
    ...playermusicState(["playedMusicList"]),
  },
  methods: {
    ...playermusicActions([
      "changeAudiocoleect",
      "changeAudioindex",
      "changeAudioSrc",
      "parseLyric",
    ]),
    back() {
      this.$router.push({ name: "My" });
    },
    
    playAudio(item, index) {
      

      if (item.url == null) {
        this.$toast("因api不提供url原因，该资源无法播放");
        return;
      }
      
      // 改变正在播放的列表
      let musicscollects = this.playedMusicList;
      this.changeAudiocoleect(musicscollects);
 
      this.axios({
        method: "GET",
        url: "/lyric",
        withCredentials: true,
        params: {
          id: item.id,
          //   id: 4341314,
        },
      })
        .then((result) => {
      console.log("请求歌词成功", result);
          let lyric;
          if (!result.data.needDesc) {
            lyric = result.data.lrc.lyric;
          } else {
            lyric = "纯音乐。请您欣赏";
          }
          
          this.parseLyric(lyric);
        })
        .catch((err) => {
          console.log(err);
        });

      this.changeAudioindex(index);

      this.changeAudioSrc(item).then((audio) => {
     
          // audio.play();
        
      });
    },
  },
};
</script>

<style lang="less" scoped>
@font-face {
  font-family: "../assets/font/iconfont";
  src: url("../assets/font/iconfont.eot");
  src: url("../assets/font/iconfont.eot?#iefix") format("embedded-opentype"),
    url("../assets/font/iconfont.woff2") format("woff2"),
    url("../assets/font/iconfont.woff") format("woff"),
    url("../assets/font/iconfont.ttf") format("truetype"),
    url("../assets/font/iconfont.svg#iconfont") format("svg");
}
</style>