<template>
<div class="daily" :class="[ischeak?'active':'']">
    <div class="bg" :style="dailybg"></div>
    <van-nav-bar left-arrow @click-left="onClickLeft" fixed>
        <template #left>
            <div class="iconfont">&#xe501;</div>
            <div class="tuij" v-show="ischeak">每日推荐</div>
        </template>
    </van-nav-bar>
    <div class="time">
        <span class="month">{{month}}</span>
        <span class="day">/{{day}}</span>
    </div>

    <van-sticky offset-top="46" @scroll="scroll">
        <div class="datily-box">
            <van-sticky offset-top="46" @scroll="scroll">
                <div class="datily-box-top" @click="randomthismusic">
                    <div class="iconfont">&#xe665;</div>
                    <div class="playall">播放全部</div>
                </div>
            </van-sticky>
            <div class="datily-box-body">
                <div class="datily-list" v-for="(value,index) in arr" :key="index" @click="playAudio(value,index)">
                    <div class="img">
                        <img :src="value.al.picUrl" alt />
                    </div>
                    <div class="datily-name">
                        <div class="name-title">{{value.name}}</div>
                        <div class="title-author">{{value.ar[0].name}}-{{value.al.name}}</div>
                    </div>
                    <div class="dataily-set">
                        <div class="audio iconfont">&#xe608;</div>
                        <div class="fenxiang iconfont">&#xe609;</div>
                    </div>
                </div>
            </div>
        </div>
    </van-sticky>
</div>
</template>

<script>
import "../../assets/css/daily.less";
import {
    createNamespacedHelpers,
    mapState,
    mapMutations,
    mapActions,
} from "vuex";

const {
    mapState: playermusicState,
    mapActions: playermusicActions,
} = createNamespacedHelpers("playermusicstore");
export default {
    data() {
        return {
            dailySongs: [],
            arr: [],
            ischeak: false,
            id: [],
            month: "",
            day: ""
        };
    },
    created() {
        this.daily();
        this.gettime()
    },
    computed: {
        // 头部背景
        dailybg() {
            let playermusicstore = this.$store.state.playermusicstore;

            let picUrl = playermusicstore.bannerlist[0].pic;

            return {
                backgroundImage: `url(${picUrl}) `,
            };
        },
    },
    methods: {
        ...playermusicActions([
            "changeAudiocoleect",
            "parseLyric",
            "changeAudioindex",
            "changeAudioSrc",
            "addPlayedMusic",
            'changeseecoleect'
        ]),
        // 每日推荐
        daily() {
            let cookie = localStorage.getItem("cookie");
            this.axios({
                    method: "GET",
                    url: "/recommend/songs",
                    withCredentials: true,
                    params: {
                        cookie,
                    },
                })
                .then((result) => {
                    console.log("请求每日推荐数据成功", result);
                    if (result.data.code == 200) {
                        this.dailySongs = result.data.data.dailySongs;
                        this.dailySongs.map((v) => {
                            this.id.push(v.id);
                        });
                        this.id = this.id.toString();

                        this.geturl();
                    }
                })
                .catch((err) => {
                    console.log(err);
                });
        },
        gettime() {
            let date = new Date();
            let year = date.getFullYear();
            let month = date.getMonth() + 1;
            month = month >= 10 ? month : "0" + month;
            this.month = month
            let d = date.getDate();
            d = d >= 10 ? d : "0" + d;
            this.day = d
        },
        // 根据uid获取歌曲url
        geturl() {
            let baseMusicUrl = "https://music.163.com/song/media/outer/url?id=";
            this.axios({
                    method: "GET",
                    url: "/song/url",
                    withCredentials: true,
                    params: {
                        id: this.id,
                    },
                })
                .then((result) => {
                    console.log("歌单的url", result);
                    if (result.data.code == 200) {
                        result.data.data.map((value, index) => {
                            this.dailySongs.map((v, i) => {
                                if (v.id == value.id) {
                                    if (value.url) {
                                        v.url = baseMusicUrl + v.id;
                                    } else {
                                        v.url = null;
                                    }
                                    return;
                                }
                            });
                        });
                        this.arr = this.dailySongs;
                        let musicscollects = this.dailySongs;
                        this.changeseecoleect(musicscollects);
                    }
                })
                .catch((err) => {
                    console.log(err);
                });
        },
        onClickLeft() {
            this.$router.go(-1);
        },
        scroll(isFixed) {
            if (isFixed.isFixed) {
                this.ischeak = true;
            } else {
                this.ischeak = false;
            }
        },
        // 点击头部随机播放列表的音乐

        randomthismusic() {
            let ranindex = this.random(this.dailySongs);
            let ranitem = this.dailySongs[ranindex];
            this.playAudio(ranitem, ranindex);
        },
        // 随机函数
        random(arr) {
            return Math.floor(Math.random() * arr.length);
        },
        //点击播放音频

        playAudio(item, index) {
            console.log("item==>", item);
            console.log(!null);
            if (item.url == null) {
                this.$toast("因api不提供url原因，该资源无法播放");
                return;
            }
            // 改变正在播放的列表
            let musicscollects = this.dailySongs;
            this.changeAudiocoleect(musicscollects);

            this.axios({
                    method: "GET",
                    url: "/lyric",
                    withCredentials: true,
                    params: {
                        id: item.id,
                        //   id: 4341314,
                    },
                })
                .then((result) => {
                    console.log("请求歌词成功", result);
                    let lyric;
                    if (!result.data.needDesc) {
                        lyric = result.data.lrc.lyric;
                    } else {
                        lyric = "纯音乐。请您欣赏";
                    }
                    this.parseLyric(lyric);
                })
                .catch((err) => {
                    console.log(err);
                });
            this.changeAudioindex(index);
            this.changeAudioSrc(item).then((audio) => {
                audio.currentTime = 0;
                //添加播放的音乐
                this.addPlayedMusic(item);
            });
        },
    },
};
</script>

<style lang="less" scoped>
@font-face {
    font-family: "../../assets/font/iconfont";
    src: url("../../assets/font/iconfont.eot");
    src: url("../../assets/font/iconfont.eot?#iefix") format("embedded-opentype"),
        url("../../assets/font/iconfont.woff2") format("woff2"),
        url("../../assets/font/iconfont.woff") format("woff"),
        url("../../assets/font/iconfont.ttf") format("truetype"),
        url("../../assets/font/iconfont.svg#iconfont") format("svg");
}
</style>
