<template>
  <div class="My">
    <div class="my-title">
      <div class="title-top">
        <div class="title-left">
          <div class="title-img">
            <img :src="profiles.avatarUrl" alt />
          </div>
          <div class="title-info">
            <div class="info-name">{{profiles.nickname}}</div>
            <div class="info-vip">
              <span class="VIP">黑胶CNIIP</span>
              <span class="levea">
                <i>lv.{{profile.level}}</i>
              </span>
            </div>
          </div>
        </div>

        <div class="title-right">
          开通黑胶VIP
          <span class="iconfont">&#xe65f;</span>
        </div>
      </div>
      <div class="title-bottom">
        <div class="fivename" v-for="(value,index) in datafive" :key="index">
          <div class="iconfont" v-html="value.icon"></div>
          <div class="font-texts">{{value.title}}</div>
        </div>
      </div>
    </div>
    <div class="my-body">
      <div class="my-bodytop">
        <div class="nav">
          <div class="my-music">我的音乐</div>
          <div class>></div>
        </div>
        <div class="recommend">
          <div class="auto">
            <div class="recommend-ban">
              <div class="my-likemusci">
                <div class="my-like" @click="gomylike(myinfolike)">
                  <div class="iconfont">&#xe60a;</div>
                  <div class="likemusic">我喜欢的音乐</div>
                  <span class="heartbeat">
                    <span class="iconfont">&#xe653; 心动模式</span>
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="my-bodybom" v-if="playedMusicList.length>=1? true:!songlist == ''? true:false">
        <div class="nav">
          <div class="my-music">
            <div>最近播放</div>
          </div>
        </div>
        <div class="recently">
          <div v-if="playedMusicList.length>=1">
            <div class="recently-img" @click="randommymusic" :style="setbgsty">
              <div class="iconfont icon">&#xe665;</div>
            </div>
            <div class="recentlys" @click="gorecently()">
              <div class="resc-name">全部已播歌曲</div>
              <div class="number">{{playedMusicList.length}}首</div>
            </div>
          </div>

          <div v-if=" !songlist == '' ">
            <div class="recently-img" @click="randomwhitmusic" :style="setsonglistbgsty">
              <div class="iconfont icon">&#xe665;</div>
            </div>
            <div class="recentlys" @click="gomylike(songlist)">
              <div class="resc-name">歌单：{{songlist.name}}</div>
              <div class="number">继续播放</div>
            </div>
          </div>
        </div>
      </div>
      <div class="my-collect">
        <van-tabs v-model="active" line-height="0" animated>
          <van-tab v-for="(value,index) in collects" :key="index">
            <template #title>
              <div @click="cli(index)">{{value.title}}</div>
            </template>
            <div class="Completed">
              <div v-for="(value,index) in collectsarr" @click="gomylike(value,index)" :key="index">
                <div class="img">
                  <img :src="value.coverImgUrl" alt />
                </div>
                <div class="Completedmusic">
                  <div class="Completedname">{{value.name}}</div>
                  <div class="Completednamenum">{{value.trackCount}}首</div>
                </div>
              </div>
            </div>
          </van-tab>
        </van-tabs>
      </div>
    </div>
  </div>
</template>

<script>
import "../../assets/css/My.less";
import { createNamespacedHelpers, mapState, mapMutations } from "vuex";
const {
  mapState: infomusicMapState,
  mapMutations: infomusicMutations,
} = createNamespacedHelpers("infomusicstore");
const {
  mapState: playermusicState,
  mapActions: playermusicActions,
} = createNamespacedHelpers("playermusicstore");
export default {
  name: "My",

  data() {
    return {
      active: 0,
      datafive: [
        { title: "本地音乐", icon: "&#xe507;" },
        { title: "下载管理", icon: "&#xe65a;" },
        { title: "我的电台", icon: "&#xe6f4;" },
        { title: "我的收藏", icon: "&#xe52f;" },
        { title: "关注新歌", icon: "&#xe90b;" },
      ],
      collects: [
        { title: "创建歌单" },
        { title: "收藏歌单" },
        // { title: "歌单助手data" },
      ],
      // 我喜欢的歌单
      myinfolike: "",
      // 展示出来的歌单
      collectsarr: [],
    };
  },
  created() {
    this.eachinfo();
    this.eachinfomusic();
    this.eachinfoFM();
  },
  computed: {
    ...infomusicMapState({
      // 用户的信息
      profile: "profile",
      profiles: "profiles",
      // 个人歌单
      personagemusics: "personagemusics",
      // 我喜欢的音乐所属的歌单
      mylikemusic: "mylikemusic",
      // 收藏的的歌单
      collectmusics: "collectmusics",
    }),

    //已播放过的音乐列表
    ...playermusicState(["playedMusicList", "songlist", "songlistitem"]),
    setbgsty() {
      let picUrl;
      if (this.$store.state.playermusicstore.playedMusicList.length >= 1) {
        picUrl = this.$store.state.playermusicstore.playedMusicList[0].al
          .picUrl;
      } else {
        picUrl = "";
      }
      return {
        backgroundImage: `url(${picUrl}) `,
      };
    },
    setsonglistbgsty() {
      let picUrl;
      if (this.$store.state.playermusicstore.songlist.length != "") {
        picUrl = this.$store.state.playermusicstore.songlist.coverImgUrl;
      } else {
        picUrl = "";
      }
      return {
        backgroundImage: `url(${picUrl}) `,
      };
    },
  },
  methods: {
    ...playermusicActions([
      "changesonglist",
      "changeAudioSrc",
      "changeAudiocoleect",
      "parseLyric",
      "changeAudioindex",
      "addPlayedMusic",
    ]),
    // 获取个人信息
    eachinfo() {
      let uid = localStorage.getItem("uid");
      this.axios({
        method: "GET",
        url: "/user/detail",
        params: {
          uid,
        },
      })
        .then((result) => {
          console.log("获取个人信息", result);
          if (result.data.code == 200) {
            this.$store.commit("infomusicstore/gaininfo", result.data);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 获取个人歌单
    eachinfomusic() {
      let uid = localStorage.getItem("uid");
      // 创建我的歌单
      let mymusics = [];
      // 创建我收藏的歌单
      let qitamusics = [];
      this.axios({
        method: "GET",
        url: "/user/playlist",
        params: {
          uid,
        },
      })
        .then((result) => {
          console.log("获取个人歌单", result);
          if (result.data.code == 200) {
            result.data.playlist.map((v) => {
              if (v.userId == uid) {
                mymusics.push(v);
              } else {
                qitamusics.push(v);
              }
            });
            let mylike = mymusics[0];
            this.mylikeimg = mymusics[0].coverImgUrl;
            this.myinfolike = mymusics[0];
            mymusics.shift();

            let obj = {
              mylike,
              qitamusics,
              mymusics,
            };

            this.$store.commit("infomusicstore/gainpersonagemusics", obj);
            // this.qitamusics = qitamusics;
            // this.mymusics = musics;
            this.cli(0);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 我喜欢的歌单

    // 获取私人FM
    eachinfoFM() {
      let cookie = localStorage.getItem("cookie");
      this.axios({
        method: "GET",
        url: "/personal_fm",
        withCredentials: true,
        params: {
          cookie,
        },
      })
        .then((result) => {
          if (result.data.code == 200) {
            let arrs = result.data.data;

            this.$store.commit("infomusicstore/gainmyFM", arrs);
          }
          console.log("获取私人FM", result);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 点击切换歌单
    cli(index) {
      if (index == 0) {
        this.collectsarr = this.personagemusics;
      }
      if (index == 1) {
        this.collectsarr = this.collectmusics;
      }
    },
    // 获取点击的歌单的id
    gomylike(item, index) {
      console.log(item);
      this.$router.push({ name: "Songistdetails", query: item });
    },
    // 点击去最近播放
    gorecently() {
      this.$router.push({ name: "recentlyplay" });
    },
    // 随机播放我的喜欢列表的歌单
    randommymusic() {
      let ranindex = this.random(this.playedMusicList);
      let ranitem = this.playedMusicList[ranindex];
      this.playAudio(ranitem, ranindex, this.playedMusicList);

      setTimeout(() => {
        this.$router.push({ name: "playermusic" });
      }, 1000);
    },
    // 点击播放歌单里面的随机音乐
    randomwhitmusic() {
      let ranindex = this.random(this.songlistitem);
      let ranitem = this.songlistitem[ranindex];
      this.playAudio(ranitem, ranindex, this.songlistitem);

      setTimeout(() => {
        this.$router.push({ name: "playermusic" });
      }, 1000);
    },
    // 随机函数
    random(arr) {
      return Math.floor(Math.random() * arr.length);
    },
    // 播放音乐函数
    playAudio(item, index, arr) {
      // 改变正在播放的列表
      let musicscollects = arr;

      this.changeAudiocoleect(musicscollects);
      this.axios({
        method: "GET",
        url: "/lyric",
        withCredentials: true,
        params: {
          id: item.id,
          //   id: 4341314,
        },
      })
        .then((result) => {
          console.log("请求歌词成功", result);
          let lyric;
          if (!result.data.needDesc) {
            lyric = result.data.lrc.lyric;
          } else {
            lyric = "纯音乐。请您欣赏";
          }
          this.parseLyric(lyric);
        })
        .catch((err) => {
          console.log(err);
        });

      this.changeAudioindex(index);
      this.changeAudioSrc(item).then((audio) => {
         audio.currentTime = 0;

        //添加播放的音乐
        this.addPlayedMusic(item);
      });
    },
  },
};
</script>

<style lang="less" scoped>
@font-face {
  font-family: "../../assets/font/iconfont";
  src: url("../../assets/font/iconfont.eot");
  src: url("../../assets/font/iconfont.eot?#iefix") format("embedded-opentype"),
    url("../../assets/font/iconfont.woff2") format("woff2"),
    url("../../assets/font/iconfont.woff") format("woff"),
    url("../../assets/font/iconfont.ttf") format("truetype"),
    url("../../assets/font/iconfont.svg#iconfont") format("svg");
}
</style>