<template>
  <div class="Register">
    <van-nav-bar title="手机号注册" left-text="返回" left-arrow @click-left="onClickLeft" />
    <van-field v-model="userinfo.tel" type="tel" label="手机号" placeholder="请输入您的手机号" />
    <van-field v-model="userinfo.password" type="password" label="密码" placeholder="请输入6位数以上的密码" />
    <van-field v-model="userinfo.nickname" type="text" label="昵称" placeholder="请输入您的昵称" />
    <van-button type="danger" @click="login">下一步</van-button>
  </div>
</template>

<script>
import { validForm } from "../../assets/js/validForm";

import { utils } from "../../assets/js/utils";
export default {
  data() {
    return {
      userinfo: {
        password: "",
        tel: "",
        nickname: "",
      },
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    login() {
      let userInfo = this.userinfo;
      //    创建信息对象,使用验证方法判断信息是否符合正则表达式
      let o = {
        phone: {
          value: userInfo.tel,
          reg: /^1[3-9]\d{9}$/,
          errorMsg: "手机号格式不正确",
        },
        password: {
          value: userInfo.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errorMsg: "密码格式不正确",
        },
      };
      // 判断验证方法返回的值
      if (!validForm.valid(o)) {
        //验证不通过
        return;
      }


      // 调用序列化方法
      //   let data = utils.queryString(info);

      this.axios({
        methods: "GET",
        url: "/cellphone/existence/check",
        params: {
          phone: userInfo.tel,
        },
      })
        .then((result) => {
          console.log(result.data.exist);
          if (result.data.exist == 1) {
            alert("用户已存在,请登录");
            this.$router.push({ name: "Login", query: { userInfo: userInfo } });
          }
          if (result.data.exist == -1) {
            this.axios({
              methods: "GET",
              url: "/captcha/sent",
              params: {
                phone: userInfo.tel,
              },
            })
              .then((res) => {
                console.log("已发");
                console.log(res);
                this.$router.push({
                  name: "Register2",
                  query: { userInfo: userInfo },
                });
              })
              .catch((a) => {
                console.log("发送验证码出现错误");
              });
          }
        })
        .catch((a) => {
          console.log("监测账号是否存在出现错误");
        });
    },
  },
};
</script>

<style lang="less" scoped>
.Register {
  /deep/ .van-nav-bar {
    background-color: #db2c1f;
    color: white;
    .van-icon {
      color: white;
    }
    .van-nav-bar__text {
      color: white;
    }
    .van-nav-bar__title {
      color: white;
    }
  }
  /deep/ .van-button {
    margin-top: 30px;
  }
}
</style>