<template>
  <div class="phoword">
    <van-nav-bar fixed left-arrow @click-left="onClickLeft">
      <template #left>
        <div class="iconfont">&#xe501;</div>
      </template>
      <template #title>
        <div>手机号登录</div>
      </template>
    </van-nav-bar>
    <div class="phone">
      <input type="password" placeholder="请输入密码" @input="changes" v-model="userinfo.password" />
      <span>忘记密码?</span>
    </div>
    <div class="btn">
      <van-button type="warning" @click="next" :disabled="disabled">登录</van-button>
    </div>
  </div>
</template>

<script>
// import { Toast } from 'vant';
import {utils} from "../../assets/js/utils"
import {mapState} from "vuex"
export default {
  data() {
    return {
      userinfo: {
        password: "",
      },
      disabled: true,
    };
  },
  computed:{
  phone(){
    return this.$store.state.infostore.phone
  }
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    next() {
      let phone = this.phone;
      let password = this.userinfo.password;
      let data = utils.queryString({
        phone,
        password,
      });

      this.axios({
        method: "POST",
        url: "/login/cellphone",
        data,
      }).then((result) => {
          console.log(result);
          if (result.data.code == 200) {
            this.$toast("登录成功");
            localStorage.setItem("uid", result.data.account.id);
            localStorage.setItem("cookie", result.data.cookie);
            this.$router.push({ name: "My" });
          }
          if (result.data.code == 502) {
            this.$toast(result.data.msg);
          }
        })
        .catch((a) => {
          console.log("监测账号是否存在出现错误");
        });
      
    },
    changes() {
      if (this.userinfo.password == "") {
        this.disabled = true;
      } else {
        this.disabled = false;
      }
    },
  },
};
</script>

<style lang="less" scoped>
.phoword {
  padding: 50px 10px 0;
}
.phone {
  position: relative;
  text-align: left;
  padding: 5px 0;
  border-bottom: 1px solid #d9d9d9;
  input {
    width: 100%;
    border: 0;
  }
  span {
    position: absolute;
    right: 10px;
    top: 0;
  }
}
.btn {
  margin-top: 20px;
  text-align: center;

  /deep/ .van-button {
    width: 100%;
    background-color: #ff3a3a;
    border-radius: 40px;
  }
}
</style>