<template>
  <div class="Loging">
    <van-nav-bar fixed left-arrow @click-left="onClickLeft">
      <template #left>
        <div class="iconfont">&#xe501;</div>
      </template>
      <template #title>
        <div>手机号登录</div>
      </template>
    </van-nav-bar>
    <div class="hint">未注册的手机号登录后将自动创建账号</div>

    <div class="phone">
      <span class="pholeft">
        +86
        <span class="iconfont">&#xe50d;</span>
      </span>
      <input type="text" placeholder="请输入手机号" v-model="userinfo.tel" />
      <span class="fr">x</span>
    </div>

    <div class="btn">
      <van-button type="warning" @click="next">下一步</van-button>
    </div>
  </div>
</template>

<script>
import { validForm } from "../../assets/js/validForm";
import { utils } from "../../assets/js/utils";
import { createNamespacedHelpers, mapState, mapMutations } from "vuex";
const {
  mapState: infoMapState,
  mapMutations: infoMutations,
} = createNamespacedHelpers("infostore");
export default {
  data() {
    return {
      userinfo: {
        tel: "",
      },
    };
  },
  computed: {
    ...infoMapState({
      phone: "phone",
    }),
  },

  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    next() {
      let userInfo = this.userinfo;
      //    创建信息对象,使用验证方法判断信息是否符合正则表达式
      let o = {
        phone: {
          value: userInfo.tel,
          reg: /^1[3-9]\d{9}$/,
          errorMsg: "手机号格式不正确",
        },
      };
      // 判断验证方法返回的值
      if (!validForm.valid(o)) {
        //验证不通过
        return;
      }

      this.axios({
        method: "GET",
        url: "/cellphone/existence/check",
        params: {
          phone: userInfo.tel,
        },
      }).then((result) => {
        console.log(result.data.exist);
        if (result.data.exist == 1) {
          // this.$store.commit("loging", this.userinfo.tel);
          this.$store.commit("infostore/loging", this.userinfo.tel);
          this.$router.push({ name: "Phonepassworld" });
        }
        if (result.data.exist == -1) {
          this.axios({
            method: "GET",
            url: "/captcha/sent",
            params: {
              phone: userInfo.tel,
            },
          }).then((res) => {
              console.log(res);
              // this.$store.commit("loging", );
              this.$store.commit("infostore/loging", this.userinfo.tel);
              this.$router.push({
                name: "Register2",
              });
            })
            .catch((a) => {
              console.log("发送验证码出现错误");
            });
        }
      });
    },
  },
};
</script>

<style lang="less" scoped>
@font-face {
  font-family: "../../assets/font/iconfont";
  src: url("../../assets/font/iconfont.eot");
  src: url("../../assets/font/iconfont.eot?#iefix") format("embedded-opentype"),
    url("../../assets/font/iconfont.woff2") format("woff2"),
    url("../../assets/font/iconfont.woff") format("woff"),
    url("../../assets/font/iconfont.ttf") format("truetype"),
    url("../../assets/font/iconfont.svg#iconfont") format("svg");
}
.Loging {
  padding: 50px 20px 0;
  text-align: left;

  .hint {
    color: #999999;
  }

  .phone {
    padding: 10px 10px 5px 0;
    border-bottom: 1px solid #d9d9d9;

    .pholeft {
      margin-right: 5px;
    }

    input {
      border: 0;
    }

    .fr {
      float: right;
      font-size: 16px;
    }
  }

  .btn {
    margin-top: 20px;
    text-align: center;

    /deep/ .van-button {
      width: 100%;
      background-color: #ff3a3a;
      border-radius: 40px;
    }
  }
}
</style>