<template>
  <div class="checkcode">
    <van-nav-bar title="填写验证码" left-text="返回" left-arrow @click-left="onClickLeft" />
    <div class="checkcode-message">
      <div>你的手机号:+86{{phone}}</div>
      <div>你会收到一条带有验证码的短信,请输入验证码</div>
    </div>
    <div class>
      <!-- 密码输入框 -->
      <van-password-input
        :value="value"
        :length="4"
        :gutter="15"
        :mask="false"
        :focused="showKeyboard"
        @focus="showKeyboard = true"
      />
      <!-- 数字键盘 -->
      <van-number-keyboard
        :show="showKeyboard"
        @input="onInput"
        @delete="onDelete"
        @blur="showKeyboard = false"
      />
    </div>
        <van-button type="danger" @click="login">下一步</van-button>
  </div>
</template>

<script>

import {mapState} from "vuex"
export default {
  data() {
    return {
     
      userInfo: "",
      value: "",
      showKeyboard: true,
    };
  },
  computed: {
    ...mapState({
          phone(){
            if (this.$store.state.infostore.phone=="") {
              return ""
            }
              return  this.$store.state.infostore.phone.substr(0,3)+"****"+this.$store.state.infostore.phone.substr(7);
          }

    }),
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1)
    },
    onInput(key) {
      this.value = (this.value + key).slice(0, 6);
    },
    onDelete() {
      this.value = this.value.slice(0, this.value.length - 1);
    },
    login(){
      console.log(12111);
    }
  },
};
</script>

<style lang="less" scoped>
.checkcode {
  /deep/ .van-nav-bar {
    background-color: #db2c1f;
    color: white;
    .van-icon {
      color: white;
    }
    .van-nav-bar__text {
      color: white;
    }
    .van-nav-bar__title {
      color: white;
    }
  }
  
  .checkcode-message{
      font-weight: 400;
      font-size: 16px;
  }
  /deep/ .van-password-input{
    .van-password-input__item{
        border-bottom: 1px solid #db2c1f; 
    }
  }
   /deep/ .van-button {
    margin-top: 30px;
  }
}
</style>