<template>
<div class="Songlist-details" :class="[ischeak?'active':'']">
    <van-nav-bar left-arrow fixed>
        <template #right>
            <div class="iconfont each">&#xe697;</div>
            <div class="iconfont">&#xe609;</div>
        </template>
        <template #left>
            <div class="iconfont back" @click="back">&#xe501;</div>
            <div>歌单</div>
        </template>
    </van-nav-bar>
    <div class="likelist-title">
        <div class="likelist-top">
            <div class="likelist-img">
                <img :src="collects.coverImgUrl" alt />
                <div>
                    <span class="iconfont">&#xe653;</span>
                    {{collects.playCount}}
                </div>
            </div>
            <div class="likelist-info">
                <div class="likelist-music">{{collectdetail.name}}</div>
                <div class="likelist-names">
                    <div class="likelist-title-img">
                        <img :src="collectdetailcreator.avatarUrl" alt />
                    </div>
                    <div class="likelist-name">
                        {{collectdetailcreator.nickname}}
                        <span class="iconfont">&#xe527;</span>
                    </div>
                </div>
            </div>
        </div>
        <div class="likelist-bom">
            <div v-for="(value,index) in datafour" :key="index">
                <div class="iconfont" v-html="value.ico"></div>
                <div>{{value.title}}</div>
            </div>
        </div>
    </div>

    <van-sticky offset-top="46" @scroll="scroll">
        <div class="likelist-body">
            <van-sticky offset-top="46" @scroll="scroll">
                <div class="likelist-body-title" @click.stop="randomthismusic">
                    <div class="ico">
                        <div class="iconfont">&#xe665;</div>
                    </div>
                    <div class="playall">
                        播放全部
                        <span>（共{{collects.trackCount}}首）</span>
                    </div>
                </div>
            </van-sticky>
            <div class="likelist-body-body">
                <div class="list-item" v-for="(value,index) in arr" :key="value.id" @click="playAudio(value,index)">
                    <div class="index">{{index+1}}</div>
                    <div class="likelistmusic-name" :class="{ active: !value.url}">
                        <div class="music-name">
                            <div>{{value.name}}</div>
                            <div v-show="value.alia[0]">({{value.alia[0]}})</div>
                        </div>
                        <div class="musicinfo-name">
                            <div>{{value.ar[0].name}}-{{value.al.name}}</div>
                        </div>
                    </div>
                    <div class="set">
                        <div class="iconfont">&#xe653;</div>
                        <div class="iconfont">&#xe609;</div>
                    </div>
                </div>
            </div>
        </div>
    </van-sticky>
</div>
</template>

<script>
import "../assets/css/Songistdetails.less";
import {
    createNamespacedHelpers,
    mapState,
    mapMutations
} from "vuex";
const {
    mapState: playermusicState,
    mapActions: playermusicActions,
} = createNamespacedHelpers("playermusicstore");

const {
    mapState: infomusicMapState,
    mapMutations: infomusicMutations,
} = createNamespacedHelpers("infomusicstore");
export default {
    data() {
        return {
            datafour: [{
                    title: "评论",
                    ico: "&#xe607;",
                },
                {
                    title: "分享",
                    ico: "&#xe738;",
                },
                {
                    title: "下载",
                    ico: "&#xe708;",
                },
                {
                    title: "多选",
                    ico: "&#xe615;",
                },
            ],

            //   音乐id字符串
            id: "",
            //传过来的歌单内容
            collects: "",
            //传过来的歌单内容
            collectdetail: "",
            //传过来的歌单内容所属的作者
            collectdetailcreator: "",

            //传过来的歌单里面歌曲的id
            collectsidarr: [],
            //传过来的歌单里面歌曲的url
            mymusicsurl: [],
            //传过来的歌单里面歌曲的详细内容
            mymusicsdetail: [],
            arr: "",
            settiem: false,
            ischeak: false
        };
    },
    computed: {
        // 我喜欢的音乐的所属的歌单
        ...infomusicMapState(["mylikemusic"]),
    },
    created() {
        this.collects = this.$route.query;
        console.log("this.collects", this.collects);
        this.eachinfolike();
    },
    methods: {
        // 我喜欢的id

        ...playermusicActions([
            // 改变音频url
            "changeAudioSrc",
            // 给已播放列表增加
            "addPlayedMusic",
            // 改变播放的歌单
            "changeAudiocoleect",
            // 歌词
            "parseLyric",
            // 改变下标
            "changeAudioindex",
            // 正在观察的歌单
            "changeseecoleect",
            "changesonglist",
            "changesonglistitem",
        ]),
        back() {
            this.$router.push({
                name: "My",
            });
        },
        scroll(isFixed) {
            if (isFixed.isFixed) {
                this.ischeak = true;
            } else {
                this.ischeak = false;
            }
        },
        // 我喜欢的歌单uid
        eachinfolike() {
            let cookie = localStorage.getItem("cookie");
            this.axios({
                    method: "GET",
                    url: "/playlist/detail",
                    params: {
                        id: this.collects.id,
                        cookie,
                    },
                })
                .then((result) => {
                    console.log("传过来的歌单详情（不包括url）", result);
                    if (result.data.code == 200) {
                        this.mymusicsdetail = result.data.playlist.tracks;

                        this.collectdetail = result.data.playlist;
                        this.collectdetailcreator = result.data.playlist.creator;

                        result.data.privileges.map((v) => {
                            this.collectsidarr.push(v.id);
                        });

                        this.id = this.collectsidarr.toString();
                        this.likedetails();
                    }
                })
                .catch((err) => {
                    console.log(err);
                });
        },

        // 根据uid获取歌曲url
        likedetails() {
            let baseMusicUrl = "https://music.163.com/song/media/outer/url?id=";
            this.axios({
                    method: "GET",
                    url: "/song/url",
                    withCredentials: true,
                    params: {
                        id: this.id,
                        //   id: 4341314,
                    },
                })
                .then((result) => {
                    console.log("歌单的url", result);
                    if (result.data.code == 200) {
                        result.data.data.map((value, index) => {
                            this.mymusicsdetail.map((v, i) => {
                                if (v.id == value.id) {
                                    if (value.url) {
                                        v.url = baseMusicUrl + v.id;
                                    } else {
                                        v.url = null;
                                    }
                                    return;
                                }
                            });
                        });
                        this.arr = this.mymusicsdetail;
                        let musicscollects = this.mymusicsdetail;
                        this.changeseecoleect(musicscollects);
                    }
                })
                .catch((err) => {
                    console.log(err);
                });
        },
        // 点击头部随机播放列表的音乐
        randomthismusic() {
            let ranindex = this.random(this.mymusicsdetail);
            let ranitem = this.mymusicsdetail[ranindex];
            this.playAudio(ranitem, ranindex);
        },
        // 随机函数
        random(arr) {
            return Math.floor(Math.random() * arr.length);
        },
        //点击播放音频

        playAudio(item, index) {
            console.log(!null);
            if (item.url == null) {
                this.$toast("因api不提供url原因，该资源无法播放");
                return;
            }
            // 改变正在播放的列表
            let musicscollects = this.mymusicsdetail;
            this.changeAudiocoleect(musicscollects);

            if (this.mylikemusic.id != this.collectdetail.id) {
                console.log("不同歌单就换");
                let songlist = this.collectdetail;
                this.changesonglist(songlist);
                this.changesonglistitem(musicscollects);
            }
            this.axios({
                    method: "GET",
                    url: "/lyric",
                    withCredentials: true,
                    params: {
                        id: item.id,
                        //   id: 4341314,
                    },
                })
                .then((result) => {
                    console.log("请求歌词成功", result);
                    let lyric;
                    if (!result.data.needDesc) {
                        lyric = result.data.lrc.lyric;
                    } else {
                        lyric = "纯音乐。请您欣赏";
                    }
                    this.parseLyric(lyric);
                })
                .catch((err) => {
                    console.log(err);
                });
            this.changeAudioindex(index);
            this.changeAudioSrc(item).then((audio) => {
                audio.currentTime = 0;
                //添加播放的音乐
                this.addPlayedMusic(item);
            });
        },
    },
};
</script>

<style lang="less" scoped>
@font-face {
    font-family: "../assets/font/iconfont";
    src: url("../assets/font/iconfont.eot");
    src: url("../assets/font/iconfont.eot?#iefix") format("embedded-opentype"),
        url("../assets/font/iconfont.woff2") format("woff2"),
        url("../assets/font/iconfont.woff") format("woff"),
        url("../assets/font/iconfont.ttf") format("truetype"),
        url("../assets/font/iconfont.svg#iconfont") format("svg");
}
</style>
