<template>
<div class="fine">
    <div class="swipe">
        <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
            <van-swipe-item v-for="(value,index) in bannerlist" :key="index">
                <div class="img">
                    <img :src="value.pic" alt />
                    <div class="typeTitle" :style="{backgroundColor:(value.titleColor)}">{{value.typeTitle}}</div>
                </div>
            </van-swipe-item>
        </van-swipe>
    </div>
    <div class="scroll">
        <div v-for="(value,index) in scrolldatalist" :key="index" class="scroll-list" @click="gopath(value)">
            <div class="iconfont" v-html="value.icon"></div>
            <div class="scroll-title">{{value.title}}</div>
        </div>
    </div>
    <div>
        <div>
            <div>懂你的精选歌单</div>
            <div>歌单广场</div>
        </div>
        <div>滚动</div>
    </div>
</div>
</template>

<script>
import "../../assets/css/Fine.less";
import {
    createNamespacedHelpers,
    mapState,
    mapMutations,
    mapActions,
} from "vuex";
const {
    mapState: playermusicState,
    mapActions: playermusicActions,
} = createNamespacedHelpers("playermusicstore");
export default {
    data() {
        return {
            banner: [],
            scrolldatalist: [{
                    title: "每日推荐",
                    icon: "&#xe507;",
                    name: "daily",
                },
                {
                    title: "歌单",
                    icon: "&#xe50b;",
                    name: "rankinglist",
                },
                {
                    title: "排行榜",
                    icon: "&#xe630;",
                    name: "rankinglist",
                },
                {
                    title: "电台",
                    icon: "&#xe6f4;",
                    name: "daily",
                },
            ],
        };
    },
    computed: {
        ...playermusicState(["bannerlist"])
    },
    created() {
        this.getbanner();
    },
    methods: {
        ...playermusicActions(["getbannerlist"]),
        // 跳转
        gopath(value) {
            this.$router.push({
                name: value.name
            })
        },
        // 获取发现banner数据
        getbanner() {
            this.axios({
                    method: "GET",
                    url: "/banner",
                    withCredentials: true,
                    params: {
                        type: 1,
                    },
                })
                .then((result) => {
                    console.log("新歌速", result);
                    if (result.data.code == 200) {
                        let banners = result.data.banners;
                        this.getbannerlist(banners)
                    }
                })
                .catch((err) => {
                    console.log(err);
                });
        },
    },
};
</script>

<style lang="less" scoped>
@font-face {
    font-family: "../../assets/font/iconfont";
    src: url("../../assets/font/iconfont.eot");
    src: url("../../assets/font/iconfont.eot?#iefix") format("embedded-opentype"),
        url("../../assets/font/iconfont.woff2") format("woff2"),
        url("../../assets/font/iconfont.woff") format("woff"),
        url("../../assets/font/iconfont.ttf") format("truetype"),
        url("../../assets/font/iconfont.svg#iconfont") format("svg");
}
</style>
